body {
    font-family: 'Roboto' sans-serif;
    font-weight: 400;
    font-size: $font-size-paragraf;
    color: $color-gray;
    letter-spacing: .2rem; }

.title-first {
    color: $color-white;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        text-transform: uppercase;
        margin-bottom: 1rem;
        animation-name: leftToright;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden; } // определяет видно ли заднюю грань элнментп когда он повернут к пользователю
    &__secondary {
        display: block;
        font-weight: 100;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: .5rem;
        animation-name: rightToleft;
        animation-duration: 1.7s;
        animation-delay: 1s;
        animation-timing-function: ease;
        backface-visibility: hidden;
        margin-bottom: 6.5rem; } }

.title-second {
    text-align: center;
    font-size: $font-size-h2;
    letter-spacing: 1rem;
    text-transform: uppercase;
    font-weight: 800;
    background: linear-gradient(90.05deg, rgba($color-brown-light, 0.7) 0.03%, rgba($color-brown-dark, 0.7) 99.94%);
    -webkit-background-clip: text; // настраевает границы фонового изображения
    color: transparent;
    transition: all .4s;
    backface-visibility: hidden;
    &:hover {
        transform: skewX(15deg) skewY(2deg) scale(1.1);
        text-shadow: $shadow-h2; } }

.title-trird {
    font-size: $font-size-h3;
    letter-spacing: .2rem;
    text-transform: uppercase;
    font-weight: 700; }

.paragraf {
    font-size: $font-size-paragraf;
    letter-spacing: .2rem;
    line-height: 140%;
    font-weight: 400; }
